import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PssApiProvider } from '../../providers/pss-api/pss-api';

/**
 * Generated class for the EventOwnerConfirmPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
    segment:'EventOwnerConfirm/:itsdangerousstring'
})
@Component({
  selector: 'page-event-owner-confirm',
  templateUrl: 'event-owner-confirm.html',
})
export class EventOwnerConfirmPage {
    success:number = 0;
    constructor(public navCtrl: NavController, public navParams: NavParams,public pssApi:PssApiProvider) {
        let encodedString:string = navParams.get('itsdangerousstring');
        if(encodedString){
            this.pssApi.eventOwnerCreateConfirm({},encodedString)
                .subscribe((result)=>{if(result==null){return};this.success=2})            
            
        }else{
            this.success=1
        }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EventOwnerConfirmPage');
  }

}



